import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {

  filterStatus = "XEM_TAT_CA"
  isShowform = false
  newEn = ""
  newVn = ""
  arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
  ];

  constructor() { }

  ngOnInit() {
  }

  addWord(){
    this.arrWords.unshift({
      id: this.arrWords.length + 1,
      en: this.newEn,
      vn: this.newVn,
      memorized: false
    })
    this.newVn = ""
    this.newEn = ""
    this.isShowform = false
  }

  removeWord(id: number){
    const index = this.arrWords.findIndex(word => word.id === id)
    this.arrWords.splice(index,1)
  }

  getShowStatus(memorized: boolean){
    const dkXemtatca = this.filterStatus == 'XEM_TAT_CA'
    const dkXemdanho = this.filterStatus == 'XEM_DA_NHO' && memorized
    const dkXemchuanho = this.filterStatus == 'XEM_CHU_NHO' && !memorized
    return dkXemtatca || dkXemdanho || dkXemchuanho
  }
}



